.techs {
    padding-top: 100px;
    padding-bottom: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #272727;
  }

  .techs__title {
    margin: 0 auto 90px;
  }

  .techs__subtitle {
    margin: 0 auto 23px;
    font-weight: 400;
    font-size: 50px;
    line-height: 1.16;
    text-align: center;
    letter-spacing: -0.04em;
  }

  .techs__text {
    max-width: 460px;
    margin: 0 auto 103px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.43;
    text-align: center;
    letter-spacing: -0.04em;
  }

  .techs__list {
    margin: 0 auto;
    display: flex;
  }

  .techs__list-item {
    min-width: 90px;
    height: 60px;
    margin: 0 10px 0 0;
    background: #303030;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .techs__list-item:last-child {
    margin-right: 0;
  }

  .techs__item-text {
    margin: 0 auto;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -0.04em;
  }

  @media screen and (max-width: 1024px) {
    .techs {
      padding-top: 91px;
      padding-bottom: 91px;
    }

    .techs__title {
      margin: 0 auto 80px;
    }

    .techs__text {
      margin: 0 auto 83px;
      font-size: 12px;
    }

    .techs__list-item {
      min-width: 84px;
      height: 57px;
    }

    .techs__item-text {
      font-size: 12px;
    }

    .techs__list {
      /*max-width: 178px;*/
      flex-wrap: wrap;
    }

    .techs__list-item {
      margin-top: 10px;
    }
  }

  @media screen and (max-width: 500px) {
    .techs {
      padding-top: 70px;
      padding-bottom: 70px;
    }

    .techs__title {
      margin: 0 auto 60px;
    }

    .techs__subtitle {
      font-size: 30px;
    }

    .techs__text {
      max-width: 292px;
      margin: 0 auto 50px;
      font-size: 11px;
    }

    .techs__list {
      max-width: 178px;
      flex-wrap: wrap;
    }

    .techs__list-item {
      margin: 0 10px 10px 0;
    }

    .techs__list-item:nth-of-type(2n) {
      margin: 0 0 10px 0;
    }

    .techs__list-item:last-child {
      margin: 0;
    }
  }
