.about-project {
    padding-top: 110px;
    padding-bottom: 110px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-project__title {
    margin: 0 auto 70px;
  }

  .about-project__stats {
    margin: 0 auto 110px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .about-project__stat {
    margin: 0 50px 0 0;
  }

  .about-project__stat:last-child {
    margin: 0;
  }

  .about-project__stat-title {
    margin: 0 auto 27px;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: -0.04em;
  }

  .about-project__stat-text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.42;
  }

  .about-project__graph {
    width: 100%;
  }

  .about-project__graph-intervals {
    width: 100%;
    display: flex;
  }

  .about-project__graph-interval {
    margin: 0;
    padding: 8px 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3;
    text-align: center;
  }

  .about-project__back-interval {
    width: 18.5%;
    color: #000000;
    background: #3DDC84;
  }

  .about-project__front-interval {
    width: 81.5%;
    background: #303030;
  }

  .about-project__graph-captions {
    width: 100%;
    display: flex;
  }

  .about-project__graph-caption {
    margin: 0;
    padding: 14px 14px 0 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #8B8B8B;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.21;
    text-align: center;
  }

  .about-project__back-caption {
    width: 18.5%;
  }

  .about-project__front-caption {
    width: 81.5%;
  }

  @media screen and (max-width: 1024px) {
    .about-project {
      padding-top: 90px;
      padding-bottom: 90px;
    }

    .about-project__stats {
      margin: 0 auto 93px;
    }

    .about-project__stat {
      margin: 0 30px 0 0;
    }

    .about-project__stat-title {
      margin: 0 auto 22px;
    }

    .about-project__stat-text {
      font-size: 12px;
    }

    .about-project__graph-caption {
      color: #A0A0A0;
      font-weight: 400;
    }
  }

  @media screen and (max-width: 500px) {
    .about-project {
      padding-top: 70px;
      padding-bottom: 70px;
    }

    .about-project__title {
      margin: 0 auto 60px;
    }

    .about-project__stats {
      max-width: 360px;
      margin: 0 auto 60px 0;
      flex-direction: column;
    }

    .about-project__stat {
      margin: 0 0 58px 0;
    }

    .about-project__stat:last-child {
      margin: 0;
    }

    .about-project__stat-title {
      margin: 0 auto 16px;
      font-size: 18px;
    }

    .about-project__stat-text {
      font-size: 11px;
    }

    .about-project__graph-interval {
      padding: 11px 10px;
      font-size: 11px;
    }

    .about-project__back-interval {
      width: 35%;
    }

    .about-project__front-interval {
      width: 65%;
    }

    .about-project__graph-caption {
      padding: 10px 10px 0 10px;
      font-size: 11px;
    }

    .about-project__back-caption {
      width: 35%;
    }

    .about-project__front-caption {
      width: 65%;
    }
  }
