.auth__input-container {
    margin: 0 0 5px;
    display: flex;
    flex-direction: column;
  }
  
  .auth__input-container:last-child {
    margin: 0;
  }
  
  .auth__input-label {
    display: block;
    margin: 0 0 10px;
    font-weight: 400;
    font-size: 10px;
    line-height: 1.2;
    color: #8B8B8B;
  }
  
  .auth__input {
    padding: 15px;
    font-weight: 400;
    font-size: 13px;
    line-height: 1.2;
    color: #FFFFFF;
    background-color: #2F2F2F;
    border-radius: 8px;
    border: none;
  }
  
  .auth__input::placeholder {
    color: #8B8B8B;
  }
  
  .auth__input:focus {
    outline: none;
    box-shadow: 0 0 0px 1000px #2F2F2F inset;
  }
  
  .auth__input_incorrect {
    color: #EE3465;
  }
  
  .auth__input-error {
    display: block;
    min-height: 12px;
    margin: 3px auto 0 0;
    font-weight: 400;
    font-size: 10px;
    line-height: 1.2;
    color: #EE3465;
  }