.portfolio {
    padding-top: 50px;
    padding-bottom: 106px;
    display: flex;
    flex-direction: column;
  }

  .portfolio__title {
    margin: 0 auto 28px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.42;
    color: #8B8B8B;
  }

  .portfolio__links {
    display: flex;
    flex-direction: column;
  }

  .portfolio__links-item {
    padding: 20px 0 19px;
    border-bottom: 1px solid #424242;
  }

  .portfolio__links-item:last-child {
    border-bottom: none;
  }

  .portfolio__link {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .portfolio__link-title {
    margin: 0 20px 0 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.67;
    letter-spacing: -0.04em;
  }

  .portfolio__arrow {
    margin-bottom: -8px;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.67;
  }


  @media screen and (max-width: 1024px) {
    .portfolio {
      padding-top: 46px;
      padding-bottom: 71px;
    }

    .portfolio__title {
      color: #A0A0A0;
    }

    .portfolio__links-item {
      border-bottom: 1px solid #E6E6E6;
    }

    .portfolio__link-title {
      font-size: 28px;
    }

    .portfolio__arrow {
      font-size: 28px;
    }
  }

  @media screen and (max-width: 500px) {
    .portfolio {
      padding-top: 0px;
      padding-bottom: 51px;
    }

    .portfolio__title {
      margin: 0 auto 20px 0;
      font-size: 14px;
    }

    .portfolio__link-title {
      margin: 0 15px 0 0;
      font-size: 18px;
    }

    .portfolio__arrow {
      font-size: 18px;
    }
  }
