.popup {
    display: none;
    position: fixed;
    padding: 20px;
    inset: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .5);
    z-index: 999;
  }
  
  .popup_opened {
    display: flex;
  }
  
  .popup__container {
    width: 100%;
    max-width: 300px;
    min-height: 120px;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    background-color: #222222;
  }
  
  .popup__text {
    margin: 0 auto;
    padding: 0;
    text-align: center;
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  
  .popup__close {
    position: absolute;
    top: -30px;
    left: 300px;
    width: 22px;
    min-height: 22px;
    outline: none;
    border: none;
    background: transparent url('../../images/close-icon.svg') center center / cover no-repeat;
    transition: opacity .2s linear;
  }
  
  @media screen and (max-width: 768px) {
    .popup {
      padding: 3px;
    }
  
    .popup__container {
      max-width: 235px;
    }
  
    .popup__text {
      max-width: 220px;
      font-size: 18px;
    }
  
    .popup__close {
      top: -20px;
      left: 235px;
    }
  }