.footer {
    display: flex;
    padding-top: 79px;
    padding-bottom: 20px;
    min-height: 172px;
    box-sizing: border-box;
    flex-direction: column;
    font-size: 13px;
  }

  .footer__title {
    margin: 0 0 20px;
    padding-bottom: 20px;
    font-size: 13px;
    font-weight: 400;
    text-align: center;
    color: #8B8B8B;
    border-bottom: 1px solid #424242;
  }

  .footer__copy-nav {
    display: flex;
    justify-content: space-between;
  }

  .footer__copyright {
    margin: 0;
    font-weight: 400;
  }

  .footer__links {
    display: flex;
  }

  .footer__links-item {
    font-weight: 400;
    margin: 0 20px 0 0;
  }

  .footer__links-item:last-child {
    margin: 0;
  }

  @media screen and (max-width: 400px) {
    .footer {
      padding: 79px 10px 20px;
      min-height: 253px;
      font-size: 12px;
    }

    .footer__title {
      margin: 0 0 30px;
      padding-bottom: 21px;
    }

    .footer__copy-nav {
      flex-direction: column-reverse;
      align-items: center;
    }

    .footer__copyright {
      color: #8B8B8B;
    }

    .footer__links {
      flex-direction: column;
      align-items: center;
      margin: 0 0 30px;
    }

    .footer__links-item {
      display: block;
      margin: 0 0 12px;
    }
  }
