.promo {
    min-height: 524px;
    padding-top: 18px;
    padding-bottom: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #073042;
    background-image: url('../../../images/landing-logo.svg');
    background-repeat: no-repeat;
    background-position: center 40%;
    background-size: 320px;
  }

  .promo__title {
    max-width: 730px;
    margin: 0 auto 43px;
    font-size: 50px;
    font-weight: 400;
    line-height: 1.16;
    text-align: center;
    letter-spacing: -0.04em;
  }

  @media screen and (max-width: 1024px) {
    .promo {
      min-height: 784px;
      background-position: center 48.5%;
    }

    .promo__title {
      margin: 0 auto 20px;
    }
  }

  @media screen and (max-width: 500px) {
    .promo {
      padding: 18px 10px;
      min-height: 570px;
      background-size: 248px;
      background-position: center 45%;
    }

    .promo__title {
      margin: 0 auto 35px;
      max-width: 300px;
      font-size: 31px;
      line-height: 1.258;
    }
  }
