.movies-card {
    /*width: 364px;*/
    height: 281px;
    display: flex;
    flex-direction: column;
    background: #222222;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
    border-radius: 6px;
  }

  .movies-card__link {
    display: flex;
    line-height: 0;
  }

  .movies-card__img {
    width: 100%;
    /*height: 203px;*/
    object-fit: cover;
    border-radius: 6px 6px 0 0;
  }

  .movies-card__container {
    padding: 9px 16px 16px;
    display: flex;
    flex-direction: column;
  }

  .movies-card__title-container {
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .movies-card__title {
    max-width: 290px;
    margin: 0 12px 0 0;
    font-weight: 500;
    font-size: 13px;
    line-height: 1.2;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .movies-card__like-button {
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background-color: transparent;
    background-image: url('../../../images/inactive-like-icon.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 14px 12px;
  }

  .movies-card__like-button_active {
    background-image: url('../../../images/active-like-icon.svg');
  }

  .movies-card__duration {
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 1.2;
    color: #8B8B8B;
  }

  @media screen and (max-width: 1024px) {
    .movies-card {
      /*width: 339px;*/
      height: 262px;
    }

    .movies-card__img {
      height: 190px;
    }

    .movies-card__container {
      padding: 8px 14px 14px;
    }

    .movies-card__title-container {
      margin-bottom: 7px;
    }

    .movies-card__title {
      max-width: 260px;
      margin: 0 21px 0 0;
    }
  }

  @media screen and (max-width: 768px) {
    .movies-card__img {
      height: 168px;
    }

    .movies-card__container {
      padding: 7px 10px 10px;
    }

    .movies-card__title-container {
      margin-bottom: 6px;
    }

    .movies-card__title {
      max-width: 240px;
      margin: 0 6px 0 0;
      font-size: 13px;
    }

    .movies-card__duration {
      font-size: 10px;
    }
  }

