.movies {
    width: 100%;
    max-width: 1280px;
    min-width: 320px;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .movies__notFound {
    display: block;
    margin: 0 auto;
    font-size: 28px;
    font-weight: 400;
    line-height: 1.2;
    color: #8B8B8B;
  }

  .movies__error {
    display: block;
    margin: 0 auto;
    font-size: 28px;
    font-weight: 400;
    line-height: 1.2;
    color: #EE3465;
  }
